body {
  font-family: "Noto Sans JP";
}

.u-desktop {
  display: none;
}
@media screen and (max-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (max-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.l-pagetop-btn-wrapper {
  display: inline-block;
  position: fixed;
  right: 40px;
  bottom: 40px;
  z-index: 10;
  margin-top: 0;
}
.l-pagetop-btn-wrapper::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 18px;
  height: 35.5px;
  background-image: url(../images/common/under-arrow.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}